@import '../../styles/typography.scss';
@import '../../styles/vars.scss';
@import '../../styles/mixins.scss';

.cart-item {
  height: 350px;
  width: 668px;
  display: flex;
  justify-content: space-between;

  &__img-container {
    position: relative;
    height: 350px;
    width: 330px;
  }

  &__img {
    height: 100%;
    width: 100%;
    object-fit: cover;
  }

  &__info {
    width: 315px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  &__top {
    display: flex;
    justify-content: space-between;
  }

  &__title,
  &__price {
    @extend %h3;
  }

  &__title {
    &:hover {
      text-decoration: underline;
    }
    margin-right: 10px;
  }

  &__bottom {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
  }

  &__quantity {
    @extend %h4;
  }

  &__buttons {
    width: 114px;
    height: 58px;
    padding: 14px;
    border-radius: 10px;
    border: 1px solid $secondary-red;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__min-btn,
  &__plus-btn {
    font-family: 'Outfit';
    border: none;
    background-color: transparent;
    color: $secondary-red;
    display: flex;
    align-items: center;

    &:hover {
      color: $main-red;
    }
  }

  &__remove-btn {
    width: 30px;
    height: 30px;
    background-image: url('../../img/icons/trash.svg');
    @include bg-img-center-norepeat;
    border: none;
    background-color: transparent;
  }

  &__old-price {
    color: gray;
    text-decoration: line-through;
    font-size: 26px;
    font-weight: 300;
    text-align: right;
    transform: translateY(-18px);
  }
}
